import 'ScssComponents/copy-link.scss';

import { exist } from '../utils';

const copyLink = () => {
	const copyLinkUrlToClipboard = (event) => {
		event.preventDefault(); // Prevent link redirection

		const $link = event.target;
		const $urlToCopy = $link.getAttribute('href');

		window.navigator.clipboard.writeText(event.target.getAttribute('href')).then(() => {
			// Add the class to provide visual feedback
			$link.classList.add('copy_link--copied');

			// Remove the class after 3 seconds
			setTimeout(() => {
				$link.classList.remove('copy_link--copied');
			}, 3000);
		});
	};

	// Attach the click event listener to the link
	const $copyLink = document.querySelector('.js-copy-link');

	if (!exist($copyLink)) return;

	$copyLink.addEventListener('click', copyLinkUrlToClipboard);
};

export default copyLink;
